import { FormattedMessage } from "react-intl"

function Welcome() {

  return(
    <div className="title-block">
      <div className="large-title">
        <FormattedMessage
          id="welcome"
          defaultMessage=""
        />
      </div>
      <div className="title">
        <FormattedMessage
          id="links_block_title"
          defaultMessage=""
        />
        </div>
    </div>
  )
}

export default Welcome